import React from "react";
import Toggle from "../Toggle/Toggle";
import "./Navbar.css";
import { Link } from "react-scroll";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { TfiAlignJustify } from "react-icons/tfi";
import { useContext } from "react";
import { ThemeContext } from "../../context";

const Navbar = () => {
  const  {state } = useContext(ThemeContext)

let mode = state.darkMode
  return (
    <div className="n-wrapper"style={{backgroundColor: mode ? "#222" : "white", color: mode && "var(--orangeCard)", paddingBottom: mode ? "2.6rem" : null  }}>
      <h2 className="n-name" id="logo" >
        {" "}
        <Link smooth spy to="Home" activeClass="activeClass">
          Tarun
        </Link>{" "}
      </h2>
<Toggle/>
      <ul style={{ listStyleType: "none" }} id="item">
        <li>
          <Link smooth spy to="Home" activeClass="activeClass">
            Home
          </Link>
        </li>
        <li>
          <Link smooth spy to="About" activeClass="activeClass">
            About
          </Link>
        </li>
        <li>
          <Link smooth spy to="Skills" activeClass="activeClass">
            Skills
          </Link>
        </li>
        <li>
          {" "}
          <Link smooth spy to="Projects" activeClass="activeClass">
            Projects
          </Link>
        </li>

        <li>
          {" "}
          <Link spy={true} to={"Contact"} smooth={true}>
            Contact
          </Link>
        </li>
      </ul>

      <TfiAlignJustify
        id="bar"
        onClick={()=>{
          document.getElementById("item").style.right == "-300px" ? document.getElementById("item").style.right="0" : document.getElementById("item").style.right = "-300px"
       
        }}
      />
    </div>
  );
};

export default Navbar;
