import React from 'react'
import "./Product.css"

const Product = ({img,name,github,deployed}) => {
  return (
    <div className="p">
      <div className="p-browser">
        <div className="p-circle reed"></div>
        <div className="p-circle yell" ></div>
        <div className="p-circle gree"></div>
        <p id="name_url">{name}</p>
      </div>
      <a href={deployed} target="_blank" >
        <img src={img} alt="" id='projects_gif'/>
      
      </a>
     <div className="source_live">
        <div className="git">
            <a href={github} target="_blank">
            <i className="fa fa-github" id='git_log'></i>
           
             </a>
        </div>
        <div className="live">
            <a href={deployed} target="_blank">
            <i className="fa fa-external-link" id='dep_log' ></i>
            </a>
        </div>
        <div className="tech-stack">
            <p>HTML</p>
            <p>CSS</p>
            <p>JS</p>
        </div>
     </div>
     
    </div>
  )
}

export default Product